.contact_form {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  outline: 1px solid rgb(97, 105, 158);
  width: 400px;
  padding: 25px;
  border-radius: 10px;
  background-color: rgb(205, 189, 220);
}

.form_label {
  display: flex;
  flex-direction: column;
  font-size: 24px;
  font-weight: 600;
  
}

.form_input {
  height: 26px;
  width: 350px;
  border: 1px solid rgb(111, 47, 170);
  border-radius: 5px;
  margin-top: 8px;
}

.form_button {
  font-size: 14px;
  font-weight: 600;
  width: 100px;
  height: 30px;
  border: 1px solid rgb(4, 101, 40);
  border-radius: 5px;
  background-color: rgb(184, 221, 221);
  cursor: pointer;
}

.form_button:hover {
  box-shadow: 0px 0px 10px 2px #647294;
}